#app { height: 100% }
html,
body {
  position: relative;
  height: 100%;
  
}

body {
  background: #eee;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
  
}

.swiper-container {
  margin-top: 5px;
	width:590px;
	height:590px;
  top: 10%;

}

.swiper-slide {
  position: fixed;
	text-align:center;
	display:flex; /* 내용을 중앙정렬 하기위해 flex 사용 */
	align-items:center; /* 위아래 기준 중앙정렬 */
	justify-content:center; /* 좌우 기준 중앙정렬 */
	background-color:#fff;
	box-sizing:border-box
}

.swiper-slide img {
  display: block;
  width: 600px;
}

.swiper {
  width: 300px;
  height: 300px;
  position: fixed;
}



